import React, { useCallback, useState } from "react";
import styled from "styled-components";
import { DefaultDiv } from "../../../styles/styles";
import { useDispatch } from "react-redux";
import { RegisterUser } from "../../../_actions/user_action";
import { useNavigate } from "react-router-dom";
import Auth from "../../../hoc/auth";

import { Modal } from "antd";
import { Form, Input, Select, Button } from "antd";

const { Option } = Select;
function RegisterPage() {
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const [form] = Form.useForm();

    const validateEmail = useCallback((_, value) => {
        const regExp =
            /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i;
        if (!value.match(regExp)) {
            return Promise.reject(new Error("   ."));
        }
        return Promise.resolve();
    }, []);

    const validatePassword = useCallback((_, value) => {
        const regExp =
            /^(?=.*[a-zA-z])(?=.*[0-9])(?=.*[$`~!@$!%*#^?&\\(\\)\-_=+]).{8,16}$/;
        if (!regExp.test(value)) {
            return Promise.reject(
                new Error(
                    " 8~16 //  ."
                )
            );
        }
        return Promise.resolve();
    }, []);

    const onSubmitHandler = (values) => {
        console.log(values);
        let user = {
            name: values.name,
            email: values.email,
            password: values.password,
            gender: values.gender,
            intro: values.intro,
        };
        dispatch(RegisterUser(user)).then((res) => {
            if (res.payload.success) {
                navigate("/login");
            } else {
                alert("Error");
            }
        });
    };

    const success = () => {
        Modal.success({
            content: "some messages...some messages...",
        });
    };
    return (
        <MainDiv>
            <h1></h1>
            <Form
                form={form}
                name="register"
                onFinish={onSubmitHandler}
                scrollToFirstError
            >
                <Form.Item
                    name="email"
                    label=""
                    rules={[
                        {
                            required: true,
                            message: "  .",
                        },
                        { validator: validateEmail },
                    ]}
                >
                    <Input />
                </Form.Item>

                <Form.Item
                    name="password"
                    label=""
                    rules={[
                        {
                            required: true,
                            message: "  .",
                        },
                        { validator: validatePassword },
                    ]}
                    hasFeedback
                >
                    <Input.Password />
                </Form.Item>

                <Form.Item
                    name="confirm"
                    label=" "
                    dependencies={["password"]}
                    hasFeedback
                    rules={[
                        {
                            required: true,
                            message: " !",
                        },
                        ({ getFieldValue }) => ({
                            validator(_, value) {
                                if (
                                    !value ||
                                    getFieldValue("password") === value
                                ) {
                                    return Promise.resolve();
                                }

                                return Promise.reject(
                                    new Error("  .")
                                );
                            },
                        }),
                    ]}
                >
                    <Input.Password />
                </Form.Item>

                <Form.Item
                    name="name"
                    label=""
                    tooltip=" .( 8)"
                    rules={[
                        {
                            required: true,
                            message: " !",
                            whitespace: true,
                        },
                    ]}
                >
                    <Input />
                </Form.Item>

                <Form.Item
                    name="gender"
                    label=""
                    rules={[
                        {
                            required: true,
                            message: " !",
                        },
                    ]}
                >
                    <Select placeholder=" ">
                        <Option value="male"></Option>
                        <Option value="female"></Option>
                    </Select>
                </Form.Item>

                <Form.Item name="intro" label=" ">
                    <Input.TextArea showCount maxLength={100} />
                </Form.Item>

                <Form.Item>
                    <Button type="primary" htmlType="submit">
                        
                    </Button>
                </Form.Item>
            </Form>
        </MainDiv>
    );
}

export default Auth(RegisterPage, false);

const MainDiv = styled(DefaultDiv)`
    padding: 30px;
    text-align: center;
    width: 500px;
    margin: 0 auto;
`;
